.sidebar {

  &__friends {
    padding: 29px;
    overflow: scroll;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      width: 0;
    }
    
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
      
      & >.sidebar__setting {
        margin: 0;
      }
    }

    &-topic {
      font-size: 12px;
      text-transform: uppercase;
      color: #5b6779;
    }

    & .sidebar__setting {
      padding: 0;
    }
  }

  &__friend {
    position: relative;
    display: flex;
    margin-top: 17px;

    &-info {
      align-self: center;
      margin-left: 11px;
    }

    &-name {
      color: #fff;
    }

    &-status {
      display: block;
      margin-top: 5px;
      color: #5b6779;
    }

    &:first-child::after {
      content: "2";
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 23px;
      height: 23px;
      top: 50%;
      right: 0;
      border-radius: 12px;
      line-height: 22px;
      color: #fff;
      background-color: #2399f1;
      transform: translateY(-50%);
    }
  }
}